import React, { useMemo } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons';
import shade from '../utils/shade';
import { BounceLoader } from 'react-spinners';

const Tab = ({
  name,
  primaryColor,
  currentTab,
  number,
  setTab,
  icon,
  showNoti,
  loading
}) => {
  const active = useMemo(() => currentTab === number, [currentTab, number]);

  const color = active ? 'primary' : 'gray-400';
  const cursor = active ? 'default' : 'pointer';
  const iconColor = showNoti
    ? active
      ? primaryColor
      : shade(primaryColor, 0.7)
    : null;

  return (
    <li
      className="last:mr-0 text-center w-full bg-white"
      style={{
        flex: number === 3 ? 1 : 2,
      }}
    >
      <a
        className={`text-lg capitalize font-bold py-4 block leading-normal ripple flex flex-col justify-center items-center text-${color} cursor-${cursor}`}
        onClick={(e) => setTab(e, number)}
        style={{
          backgroundColor: active ? '#FFFFFF' : '#F8F8F9',
          height: '59px',
        }}
      >
        {loading ? (
          <BounceLoader
            color={number === currentTab ? primaryColor : '#9ca3af'}
            size={20}
          />
        ) : icon ? (
          showNoti ? (
            <FontAwesomeIcon
              icon={faExclamationCircle}
              size="lg"
              color={iconColor}
            />
          ) : (
            <FontAwesomeIcon icon={icon} size="lg" />
          )
        ) : (
          <></>
        )}

        {name}
      </a>
    </li>
  );
};

export default Tab;
